	background-position: center;
	transition: all  .3s ease;
	-webkit-transition: all  .3s ease;
	-moz-transition: all  .3s ease;
}
.slide .view {
	transform: translate3d(0,-40%,0);
	-webkit-transform: translate3d(0,-40%,0);
	-moz-transform: translate3d(0,-40%,0);
}
.view:after {
	content: "";
	position: absolute;
	width: 100%;
	height: 100%;
	top: 0;
	left: 0;
	opacity: 0;
	pointer-events: none;
	transition: all  .3s ease;
	-webkit-transition: all  .3s ease;
	-moz-transition: all  .3s ease;
	background: -webkit-gradient(linear,left top, left bottom,color-stop(10%, rgba(255,255,255,0)),to(rgba(251,251,251,1)));
