@-webkit-keyframes bounce-in {
	0% {
		-webkit-transform: scale(0.65);
		        transform: scale(0.65);
	}
	50% {
		-webkit-transform: scale(1.8);
		        transform: scale(1.8);
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
@keyframes bounce-in {
	0% {
		-webkit-transform: scale(0.65);
		        transform: scale(0.65);
	}
	50% {
		-webkit-transform: scale(1.8);
		        transform: scale(1.8);
	}
	100% {
		-webkit-transform: scale(1);
		        transform: scale(1);
	}
}
